import { injectGlobal } from '@emotion/css'
import { GLOBAL_STYLE_CONSTANTS } from './constants'

// fix spelling mistake in galeryDotGap, during integration gallery to web client app
injectGlobal(`
  --galeryDotSize: ${GLOBAL_STYLE_CONSTANTS.galeryDotSize}px;
  --galleryArrowSize: ${GLOBAL_STYLE_CONSTANTS.galleryArrowSize}px;
  --galeryDotGap: ${GLOBAL_STYLE_CONSTANTS.galeryDotGap}px;
  --galleryThumbnailGap: ${GLOBAL_STYLE_CONSTANTS.galleryThumbnailGap}px;

  @keyframes ripple {
    to {
      opacity: 0;
      transform: scale(4);
    }
  }

  span.cf-ripple {
    position: absolute;
    border-radius: 50%;
    transform: scale(0);
    animation: ripple 600ms linear;
    pointer-events: none;
  }

  .cf-ripple-container-light span.cf-ripple {
    background-color: rgba(255, 255, 255, 0.24);
  }

  .cf-ripple-container-dark span.cf-ripple {
    background-color: rgba(0, 0, 0, 0.075);
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover, 
  input:-webkit-autofill:focus, 
  input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px #FAFAFA inset !important;
  }
`)
